		content: '\n## \u53CC\u7EA7\u590D\u4F4D\u578B\u6DB2\u538B\u62C9\u4F38\u5668\n\n![\u53CC\u7EA7\u590D\u4F4D\u578B\u6DB2\u538B\u62C9\u4F38\u5668](http://onk96vraq.bkt.clouddn.com/15-1.png)\n\n\u53CC\u7EA7\u590D\u4F4D\u578B\u6DB2\u538B\u62C9\u4F38\u5668\u91C7\u7528\u53CC\u6CB9\u7F38\u8BBE\u8BA1\uFF0C\u662F\u9488\u5BF9\u72ED\u5C0F\u7A7A\u95F4\u4E13\u7528\u7684\u4E00\u6B3E\u6DB2\u538B\u62C9\u4F38\u5668\uFF1B\u6D3B\u585E\u81EA\u52A8\u590D\u4F4D\u8BBE\u8BA1\uFF0C\u9002\u5408\u9891\u7E41\u4F7F\u7528\u3002\n\n\u91C7\u7528\u7684\u53CC\u7EA7\u7684\u7ED3\u6784\u5F62\u5F0F\uFF0C\u727A\u7272\u8F74\u5411\u5C3A\u5BF8\u6765\u4FDD\u8BC1\u5F84\u5411\u5C3A\u5BF8\u3002\u5728\u5916\u5F84\u4E0D\u53D8\u7684\u60C5\u51B5\u4E0B\uFF0C\u662F\u6D3B\u585E\u7684\u6709\u6548\u9762\u79EF\u589E\u500D\uFF0C\u63D0\u9AD8\u4F5C\u7528\u529B\u3002\n\n\u9002\u7528\u4E8E\u4F46\u89C4\u683C\u87BA\u6813\u7684\u62C9\u4F38\u3002\u5177\u6709\u91CD\u91CF\u8F7B\uFF0C\u7CBE\u5EA6\u9AD8\uFF0C\u4F53\u79EF\u5C0F\uFF0C\u7ED3\u6784\u7D27\u51D1\u7B49\u7279\u70B9\uFF0C\u591A\u4E2A\u62C9\u4F38\u5668\u53EF\u4EE5\u540C\u65F6\u4F7F\u7528\u3002\n\n'
